const state = {
  is_show: false,
  is_modal: false,
  is_headerData: '',
  is_buttonData: '',

  //---------------- 저장한 뉴스
  is_boardTitle: '',
  is_boardList: [],
  //----------------

  //---------------- 검색 즐겨찾기
  is_bookmark_list: [],
  select_list_co: [],
  select_list_te: [],
  select_list_mv: [],
  select_list_ck: [],
  //----------------
  

}

const getters = {
  isShow(state) {
    return state.is_show;
  },
  isModal(state) {
    return state.is_modal;
  },
  isHeaderData(state) {
    return state.is_headerData;
  },
  
  //---------------- 저장한 뉴스  
  isBoardTitle(state) {
    return state.is_boardTitle;
  },
  isBoardList(state) {
    return state.is_boardList;
  },

  //---------------- 검색 즐겨찾기
  isBookmarkList(state) {
    return state.is_bookmark_list;
  },  
  selectListCo(state) {
    return state.select_list_co;
  },
  selectListTe(state) {
    return state.select_list_te;
  },
  selectListMv(state) {
    return state.select_list_mv;
  },
  selectListCk(state) {
    return state.select_list_ck;
  },
}

const mutations = {
  toggleModal(state) {
    state.is_Modal = !state.is_Modal
  },

  //-------------------------------- 저장한 뉴스  
  makeSaveBoard(state, payload) {
    let obj = {}    
    let confirmBoard = confirm(`새로운 보드 ${payload}(을)를 생성 하시겠습니까?`)   
    if (confirmBoard === true ) {
      let modal = document.getElementById('favorite-modal')
      let modal_s = document.getElementById('star_modal')
      if (modal) {
        modal.style.display = 'none';
      } else if (modal_s) {
        modal_s.style.display = 'none';
      }
      obj.title = payload
      state.is_boardList.unshift(obj)
      console.log('state.is_boardList:', state.is_boardList);
    }
  },

  makeStarSaveBoard(state, payload) {
    let obj = {}    
    let confirmBoard = confirm(`새로운 보드 ${payload}(을)를 생성 하시겠습니까?`)   
    if (confirmBoard === true ) {
      let modal = document.getElementById('favorite-modal')
      let modal_s = document.getElementById('star_modal')
      if (modal) {
        modal.style.display = 'none';
      } else if (modal_s) {
        modal_s.style.display = 'none';
      }
      obj.title = payload
      state.is_boardList.unshift(obj)    
      console.log('state.is_boardList:', state.is_boardList);
    } else {
      return false
    }
  },

  modifySaveBoard(state, payload) {
    // let obj = {}        
    console.log('payload.e:', payload.title);
    console.log('payload.index:', payload.index);
    let confirmBoard = confirm(`보드 이름을 ${payload.title}(으)로 수정 하시겠습니까?`)   
    if (confirmBoard === true ) {
      let modal = document.getElementById('modify-board')

      payload.index.title = payload.title
      modal.style.display = 'none';
    }
  },
  //--------------------------------

  //-------------------------------- 검색 즐겨찾기
  makeBookmarkBoard(state, payload) {
    let obj = {}
    let val = document.getElementById('search-title')
    let notice = document.getElementById('psk')
    
    // let board = document.getElementById('new-bookmark')
    let confirmBoard = confirm(`새로운 보드 ${payload}(을)를 생성 하시겠습니까?`)   

    if ( state.select_list_co.length === 0 && state.select_list_te.length === 0  && state.select_list_mv.length === 0 && state.select_list_ck.length === 0) {
      // alert('키워드를 선택해주시기 바랍니다.') 
      notice.style.color = 'red'
      return false
    } else if ( confirmBoard === true ) {
      let modal = document.getElementById('edit_modal')
      modal.style.display = 'none';
      val.value = '';

      obj.title = payload
      obj.contents = [];
      obj.contents.push(...state.select_list_co, ...state.select_list_te, ...state.select_list_mv, ...state.select_list_ck)

      state.is_bookmark_list.unshift(obj)

      state.select_list_co.splice(0, state.select_list_co.length);
      state.select_list_te.splice(0, state.select_list_te.length);
      state.select_list_mv.splice(0, state.select_list_mv.length);
      state.select_list_ck.splice(0, state.select_list_ck.length);
      // this.$children[0]._data.board_title_warn = false;

      console.log('obj.contents:', obj.contents);
      console.log('obj:', obj);
      console.log('state.is_bookmark_list:', state.is_bookmark_list);
    }   
  },

  modifyBookmarkBoard(state, payload) {
    let notice = document.getElementById('psk_m')
    
    let confirmBoard = confirm(`보드 이름을 ${payload.title}(으)로 수정 하시겠습니까?`)   
    
    if ( state.select_list_co.length === 0 && state.select_list_te.length === 0  && state.select_list_mv.length === 0 && state.select_list_ck.length === 0) {
      // alert('키워드를 선택해주시기 바랍니다.') 
      notice.style.color = 'red'
      return false
    } else if ( confirmBoard === true ) {
      let modal = document.getElementById('edit_modal_md')
      modal.style.display = 'none';

      payload.index.title = payload.title  
      payload.index.contents = []
      payload.index.contents.push(...state.select_list_co, ...state.select_list_te, ...state.select_list_mv, ...state.select_list_ck)

      state.select_list_co.splice(0, state.select_list_co.length);
      state.select_list_te.splice(0, state.select_list_te.length);
      state.select_list_mv.splice(0, state.select_list_mv.length);
      state.select_list_ck.splice(0, state.select_list_ck.length);
    }  
  },
  //--------------------------------  
}

const actions = {

}

export default {
  state,
  getters,
  mutations,
}


