<template>
  <div class="modal fade" ref="board" id="favorite-modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" style="display: none;">
    <div class="modal-dialog modal-md" role="document">
        <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title">보드 추가</h4>
            </div>
            <div class="modal-body">
              <div class="input-name">이름</div>
              <input :value="isBoardTitle" @keydown.enter="saveBoardTitle" ref="input" id="save-title" type="text" placeholder="보드명을 입력해 주세요" maxlength="20">
              <div v-show="board_title_warn" ref="pmt" class="p-m-t">{{ please_board_title }}</div>
            </div>
            <div class="modal-footer">
              <button @click="saveBoardTitle" id="save-board">만들기</button>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'ModalBoard',
  data() {
    return{ 
      board_title_warn: false,
      please_board_title: '',
      // input_data: '',
      board_title: '',
    }
  },
  computed: {
    ...mapGetters([
      'isBoardList',
      'isBoardTitle',
      // 'boardTitleWarn',
    ])
  },
  methods: {
    ...mapMutations([
      'makeSaveBoard',

    ]),
    saveBoardTitle() {
      let e = this.$refs.input.value.trim()
      console.log('this.$refs.input:', this.$refs.input);
      this.board_title === e
      if ( e === '' ) {
        this.board_title_warn = true
        this.please_board_title = '보드명 입력은 필수입니다.'
        this.$refs.input.focus()
      } else {
        this.board_title_warn = false
        this.makeSaveBoard(e);
      }
      // this.makeSaveBoard(e)
      // this.$emit('board_title', this.$refs.input.value) 
    },

    // resetWarn() {
    //   this.$emit('reset', this.board_title_warn)
    // }

    // MakeBoard() {
    //   let confirmBoard = confirm(`새로운 보드 ${this.input_data}를(을) 생성 하시겠습니까?`)
    //   if ( confirmBoard === true ) {
    //     this.input_data = ''
    //     this.$refs.board.style.display="none"
    //   }
    // }
  },
}
</script>

<style lang="scss" scoped>
.modal-dialog {
  position: relative;
  z-index: 9999;
  margin-top: 30vh;
  .modal-header {
    padding: 15px;
    border-bottom: 1px solid #e6e6e6;
    .modal-title {
      font-size: 18px;
    }
  }
  .modal-body {
    .input-name {
      width: 100%;
      padding: 8px 0;
    }
    #save-title {
      box-sizing: border-box;
      width: 100%;
      border: 1px solid #f0f0f0;
      background-color: #f0f0f0;
      border-radius: 4px;
      padding: 10px 14px;
      word-spacing: 0;
      letter-spacing: -.2px;
      line-height: 18px;
      font-size: 16px;
      color: #545454;
      font-weight: 700;
    }
    .p-m-t {
      color: red;
      font-size: 12px;
      padding-top: 5px;
      padding-left: 15px;
    }
  }
  .modal-footer {
    #save-board {
      padding: 11px 14px;
      border-radius: 4px;
      background-color: #FF8C00;
      color: #fff;
      font-size: 14px;
      text-align: center;
      vertical-align: middle;
      white-space: nowrap;
      touch-action: manipulation;
      cursor: pointer;
      border: 1px solid #FF8C00;
    }
  }
}
</style>




