<template>
  <div id="nav">
    <nav class="container" role="navigation">
      <a href="" @click="goToMain" class="nav-logo">
        <img id="logo" src="../assets/find_big_five.png" alt="Find Big 5 logo">
      </a>
      <div class="nav-list">
        <ul class="company-list">
          <li @click="isToggleCo">회사별 동향</li>
          <transition name="nav-bar" mode="out-in">       
            <ul id="co" class="show-company" v-if="isShowCo">
              <router-link id="co-ama" ref="co" class="company" to="/category/amazon" tag="li">Amazon</router-link>
              <router-link id="co-app" ref="co" class="company" to="/category/apple" tag="li">Apple</router-link>
              <router-link id="co-fb" ref="co" class="company" to="/category/facebook" tag="li">Facebook</router-link>
              <router-link id="co-gg" ref="co" class="company" to="/category/google" tag="li">Google</router-link>
              <router-link id="co-ms" ref="co" class="company" to="/category/microsoft" tag="li">Microsoft</router-link>
            </ul>
          </transition>        
        </ul>
        <ul class="tech-list">                                  
          <li @click="isToggleTe">기술별 동향</li>      
          <transition name="nav-bar" mode="out-in">                  
            <ul id="te" class="show-tech" v-if="isShowTe">
              <router-link id="te-ai" class="tech" to="/category/ai" tag="li">AI</router-link>
              <router-link id="te-bd" class="tech" to="/category/big-data" tag="li">Big-data</router-link>
              <router-link id="te-cl" class="tech" to="/category/cloud" tag="li">Cloud</router-link>
              <router-link id="te-ec" class="tech" to="/category/e-commerce" tag="li">E-commerce</router-link>
              <router-link id="te-sd" class="tech" to="/category/iot" tag="li">Smart devices</router-link>          
              <router-link id="te-vr" class="tech" to="/category/vr" tag="li">VR/AR</router-link>          
            </ul>
          </transition>
        </ul>
        <div class="search">
          <ul class="search">
            <router-link id="nav-search" to="/search_favorite" tag="li">검색 즐겨찾기</router-link>
          </ul>
        </div>
        <div class="save">        
          <ul class="save">
            <router-link id="nav-save" to="/save_board" tag="li">저장한 뉴스</router-link>
          </ul>
        </div>
      </div>
    </nav>
    <div class="logout">
      <button type="button">
        로그아웃
        <span class="fa fa-sign-out" aria-hidden="true"></span>
      </button>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapMutations, mapActions} from 'vuex'

export default {
  name: 'NavMenu',
  watch: {
    // beforeRouteUpdate (to, from, next) {
    //   this.id = to.params.id;
    //   this.isToggleRoute()
    // },
    '$route' (to, from) {
      this.id = to.params.id;
      this.isToggleRoute()
    }
  },
  // created () {
  //   this.$route.watch(
  //     () => {
  //       return this.$route.params.id 
  //     },
  //     val => {
  //       this.isToggleCo();
  //     },
  //     { deep: true }
  //   );
  // },
  data() {
    return {
      isShowCo: false,
      isShowTe: false,
      id: this.$route.params.id
    }
  },
  methods: {
    isToggleRoute() {
      let ama = document.getElementById('co-ama');
      let app = document.getElementById('co-app');
      let fb = document.getElementById('co-fb');
      let gg = document.getElementById('co-gg');
      let ms = document.getElementById('co-ms');

      let ai = document.getElementById('te-ai');
      let bd = document.getElementById('te-bd');
      let cl = document.getElementById('te-cl');
      let ec = document.getElementById('te-ec');
      let sd = document.getElementById('te-sd');
      let vr = document.getElementById('te-vr');

      let search = document.getElementById('nav-search');
      let save = document.getElementById('nav-save');
      if ('amazon'.indexOf(this.$route.params.id) > -1 ) {
        ama.style.color = '#ff8c00'
        app.style.color = '#000'
        fb.style.color = '#000'
        gg.style.color = '#000'
        ms.style.color = '#000'
        // this.$refs.co.style.color = '#ff8c00'
        // console.log('this.$refs.co:', this.$refs.co);
        this.isShowCo = true;
        this.isShowTe = false;
      } else if ('apple'.indexOf(this.$route.params.id) > -1 ) {
        app.style.color = '#ff8c00'
        ama.style.color = '#000'
        fb.style.color = '#000'
        gg.style.color = '#000'
        ms.style.color = '#000'
        this.isShowCo = true;
        this.isShowTe = false;
      } else if ('facebook'.indexOf(this.$route.params.id) > -1 ) {
        fb.style.color = '#ff8c00'
        ama.style.color = '#000'
        app.style.color = '#000'
        gg.style.color = '#000'
        ms.style.color = '#000'
        this.isShowCo = true;
        this.isShowTe = false;
      } else if ('google'.indexOf(this.$route.params.id) > -1 ) {
        gg.style.color = '#ff8c00'
        ama.style.color = '#000'
        app.style.color = '#000'
        fb.style.color = '#000'
        ms.style.color = '#000'
        this.isShowCo = true;
        this.isShowTe = false;
      } else if ('microsoft'.indexOf(this.$route.params.id) > -1 ) {
        ms.style.color = '#ff8c00'
        ama.style.color = '#000'
        app.style.color = '#000'
        fb.style.color = '#000'
        gg.style.color = '#000'
        this.isShowCo = true;
        this.isShowTe = false;
      } else if ('ai'.indexOf(this.$route.params.id) > -1 ) {
        ai.style.color = '#ff8c00'
        bd.style.color = '#000'
        cl.style.color = '#000'
        ec.style.color = '#000'
        sd.style.color = '#000'
        vr.style.color = '#000'
        this.isShowTe = true;
        this.isShowCo = false;
      } else if ('big-data'.indexOf(this.$route.params.id) > -1 ) {
        bd.style.color = '#ff8c00'
        ai.style.color = '#000'
        cl.style.color = '#000'
        ec.style.color = '#000'
        sd.style.color = '#000'
        vr.style.color = '#000'
        this.isShowTe = true;
        this.isShowCo = false;
      } else if ('cloud'.indexOf(this.$route.params.id) > -1 ) {
        cl.style.color = '#ff8c00'
        ai.style.color = '#000'
        bd.style.color = '#000'
        ec.style.color = '#000'
        sd.style.color = '#000'
        vr.style.color = '#000' 
        this.isShowTe = true;
        this.isShowCo = false;
      } else if ('e-commerce'.indexOf(this.$route.params.id) > -1 ) {
        ec.style.color = '#ff8c00'
        ai.style.color = '#000'
        cl.style.color = '#000'
        bd.style.color = '#000'
        sd.style.color = '#000'
        vr.style.color = '#000' 
        this.isShowTe = true;
        this.isShowCo = false;
      } else if ('iot'.indexOf(this.$route.params.id) > -1 ) {
        sd.style.color = '#ff8c00'
        ai.style.color = '#000'
        cl.style.color = '#000'
        ec.style.color = '#000'
        bd.style.color = '#000'
        vr.style.color = '#000' 
        this.isShowTe = true;
        this.isShowCo = false;
      } else if ('vr'.indexOf(this.$route.params.id) > -1 ) {
        vr.style.color = '#ff8c00'
        ai.style.color = '#000'
        cl.style.color = '#000'
        ec.style.color = '#000'
        sd.style.color = '#000'
        bd.style.color = '#000' 
        this.isShowTe = true;
        this.isShowCo = false;
      } else if ('search_favorite'.indexOf(this.$route.params.id) > -1 || this.isShowCo === true || this.isShowTe === true) {
        this.isShowCo = false;
        this.isShowTe = false;        
      }
      // } else if ('save_board'.indexOf(this.$route.params.id) > -1) {
      //   this.isShowCo = false;
      //   this.isShowTe = false; 
      // }
    },
    changeColor(e) {
      e.target.classList.add('fb5-color')
      console.log('e.target:', e.target);
    },
    isToggleCo() {
      this.isShowCo = !this.isShowCo;
      // console.log('prams',this.$route.params)
      // if ( this.isShowCo === true ) {
        // this.isShowTe = false;
        // let co = document.getElementById('co')
        // co.style.display = 'none'
        // this.$refs.co.style.display='none'
      // }
    },
    isToggleTe() {
      this.isShowTe = !this.isShowTe;
      // if ( this.isShowTe === true ) {
        // this.isShowCo = false;
        // let te = document.getElementById('te')
        // te.style.display = 'none'
      // }
    },
    goToMain() {
      this.$router.push( { path: '/main' } )
    },
  }
}
</script>

<style lang="scss" scoped>

//------------------------ transition
.nav-bar-enter-active,
.nav-bar-leave-active {
  transition: all .25s linear;
}
// .nav-bar-leave-active {
//   transition: all .25s cubic-bezier(1.0, 0.5, 0.8, 1.0);
// }
.nav-bar-enter {
  transform: translateY(-10px);
  opacity: 0;  
}
.nav-bar-leave-to {
  transform: translateY(-10px);
  opacity: 0;
}

//------------------------
.fb5-color {
  color: #ff8c00;
}

.container {
  margin: 0;
  padding: 0;
  height: auto;
  width: 200px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}
.box {
  width: 200px;
  height: 500px;
  padding: 0;
  margin: 0;
  background: #fff;  
}

.nav-logo {
  display: inline-block;
  width: 200px;
  text-align: center;
  #logo {
    height: 70px;
    line-height: 70px;
    vertical-align: middle;      
    margin: 35px 0;
  }
}

.nav-list {
  #company, 
  #tech {
    cursor: pointer;
  }
  ul {
    text-align: center;
    transition: opacity .15s linear;    
  }
  a { 
    // font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-family: 'Roboto', sans-serif;    
    font-size: 14px;
    text-decoration: none;  
    line-height: 1.42857143;
    color: #000;         
  }
  li {
    cursor: pointer;    
    width: 127px;
    height: 33px;
    display: inline-block;
    text-align: left;  
    vertical-align: middle;
    padding: 8px 12px;
  }
}
.company-list {
  .company {
    padding-left: 25px;
  }
  li:hover {
    background-color: #EAEDED;
  }
}
.tech-list {
  .tech {
    padding-left: 25px;
  }
  li:hover {
    background-color: #EAEDED;
  }
}
.search {
  li:hover {
    background-color: #EAEDED;
  }
}
.save {
  li:hover {
    background-color: #EAEDED;
  }
}
.clicked {
  background-color: #ffbb60;
}
.logout {
  color: #305050;
  position: fixed;
  bottom: 20px;
  left: 45px;
  button {
    padding: 0 25px 0 25px;
    background: none;
    border: none;
    font-size: 13.5px;
    font-weight: 600;
  }
}
</style>






