<template>
  <div class="modal fade" ref="board_md" id="modify-board" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" style="display: none;">
    <div class="modal-dialog modal-md" role="document">
        <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title">보드 수정</h4>
            </div>
            <div class="modal-body">
              <div class="input-name">이름</div>
              <input ref="input" @keydown.enter="ModifyBoard" id="modify-save-title" type="text" placeholder="수정할 보드명을 입력해 주세요" maxlength="20" value="">
              <div v-show="board_title_warn" ref="pmt" class="p-m-t">{{ please_board_title }}</div>
            </div>
            <div class="modal-footer">
              <button @click="ModifyBoard" id="save-board">수 정</button>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  props: ['index'],
  name: 'ModalBoardModify',
  data() {
    return{ 
      board_title_warn: false,
      please_board_title: '',
    }
  },
  computed: {
    ...mapGetters([
      'isBoardList',
      'isBoardTitle',
    ])
  },
  methods: {
    ...mapMutations([
      'modifySaveBoard',
    ]),

    ModifyBoard(index) {
      let obj = { 
        title: this.$refs.input.value.trim(),
        index: this.index
      }
      if ( obj.title === '' ) {
        this.board_title_warn = true
        this.please_board_title = '보드명 입력은 필수입니다.'
        this.$refs.input.focus()
      } else {
        this.board_title_warn = false
        this.modifySaveBoard(obj);
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-dialog {
  position: relative;
  z-index: 9999;
  margin-top: 30vh;
  .modal-header {
    padding: 15px;
    border-bottom: 1px solid #e6e6e6;
    .modal-title {
      font-size: 18px;
    }
  }
  .modal-body {
    .input-name {
      width: 100%;
      padding: 8px 0;
    }
    #modify-save-title {
      box-sizing: border-box;
      width: 100%;
      border: 1px solid #f0f0f0;
      background-color: #f0f0f0;
      border-radius: 4px;
      padding: 10px 14px;
      word-spacing: 0;
      letter-spacing: -.2px;
      line-height: 18px;
      font-size: 16px;
      color: #545454;
      font-weight: 700;
    }
    .p-m-t {
      color: red;
      font-size: 12px;
      padding-top: 5px;
      padding-left: 15px;
    }
  }
  .modal-footer {
    #save-board {
      padding: 11px 14px;
      border-radius: 4px;
      background-color: #FF8C00;
      color: #fff;
      font-size: 14px;
      text-align: center;
      vertical-align: middle;
      white-space: nowrap;
      touch-action: manipulation;
      cursor: pointer;
      border: 1px solid #FF8C00;
    }
  }
}
</style>




