<template>
  <div class="bookmark">
    <div class="article-container">
      <div class="section-header col-xs-12">
        <h1 class="main-header">
          <span>_</span>
          검색 즐겨찾기
          <button class="help-bookmark"data-toggle="modal" data-target="#help-modal">
            <span class="fa fa-question-circle" aria-hidden="true"></span>
            <span class="help-text">즐겨찾기란</span>
          </button>
        </h1>
      </div>
      <div class="article-bookmark">
        <div class="row">
          <div class="col-xs-12">
            <div class="article-bookmark-list">
              <article>
                <ul class="board-list">
                  <li id="new-bookmark" v-for="(list, index) in isBookmarkList" class="board-added">
                    <h4 @click="goToArticle(index)" class="board-title">{{ list.title.toString() }}
                      <span class="list-numbers">0</span>
                    </h4>
                    <div class="list-group">
                      <p @click="goToArticle(index)" class="list-name">{{ list.contents.toString() }}</p>
                      <div class="list-btn-group">
                        <button @click="modifyModal(index)" class="add-list" type="button" data-toggle="modal" data-target="#edit_modal_md">
                          <i @click="resetModifyModal" class="fa fa-cog fa-lg" aria-hidden="true"></i>
                        </button>
                        <button @click="removeBoard" class="list-delete" type="button">
                          <i class="fa fa-trash fa-lg" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </li>
                  <li @click="resetModal" class="board board-new" data-toggle="modal" data-target="#edit_modal">
                    <div class="add-keyword">즐겨찾기 추가</div>
                    <div class="add-btn-group">
                      <button class="add-list" type="button">
                        <span class="fa-stack hightlight">
                          <i class="fa fa-circle-thin fa-stack-2x" aria-hidden="true"></i>
                          <i class="fa fa-plus fa-stack-1x" aria-hidden="true"></i>
                        </span>
                      </button>
                    </div>
                  </li>
                  <!-- <li class="board-added">
                    <h4 class="board-title">Google/FB 인공지능 비교
                      <span class="list-numbers">37</span>
                    </h4>
                    <div class="list-group">
                      <span class="list-name">Google, Facebook, AI</span>
                      <div class="list-btn-group">
                        <button class="add-list" type="button" data-toggle="modal" data-target="#edit_modal">
                          <i class="fa fa-cog fa-lg" aria-hidden="true"></i>
                        </button>
                        <button class="list-delete" type="button">
                          <i class="fa fa-trash fa-lg" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </li>
                  <li class="board-added">
                    <h4 class="board-title">VR / AI 관련
                      <span class="list-numbers">10</span>
                    </h4>
                    <div class="list-group">
                      <span class="list-name">VR, AI</span>
                      <div class="list-btn-group">
                        <button class="add-list" type="button" data-toggle="modal" data-target="#edit_modal">
                          <i class="fa fa-cog fa-lg" aria-hidden="true"></i>
                        </button>
                        <button class="list-delete" type="button">
                          <i class="fa fa-trash fa-lg" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </li> -->
                </ul>
              </article>
            </div>
          </div>
        </div>
      </div>
    </div>
    <modal-search></modal-search>
    <modal-guide></modal-guide>
    <modal-search-modify :index="index"></modal-search-modify>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

import ModalSearch from './ModalSearch'
import ModalSearchModify from './ModalSearchModify'
import ModalGuide from './ModalGuide'

export default {
  name: 'MainBookmark',
  components : {
    ModalSearch, ModalSearchModify, ModalGuide
  },
  data() {
    return {
      index: '',
    }
  },
  computed: {
    ...mapGetters([
      'isModal',
      'isBookmarkList',
      'selectListCo',
      'selectListTe',
      'selectListMv',
      'selectListCk',
    ])
  },
  methods: {
    ...mapMutations([
      // 'resetInputValue'
    ]),
    resetModifyModal() {
      let notice = document.getElementById('psk_m')
      console.log('notice.style.color:', notice.style.color);
      if(notice.style.color === 'red') {
        notice.style.color = '#b3b3b3'
      }
    },
    resetModal() {
      let val = document.getElementById('search-title');
      let ck = document.getElementById('custom-keyword');
      let notice = document.getElementById('psk')

      val.value = '';
      ck.value = '';
      notice.style.color = '#b3b3b3'
      this.selectListCo.splice(0, this.selectListCo.length);
      this.selectListTe.splice(0, this.selectListTe.length);
      this.selectListMv.splice(0, this.selectListMv.length);
      this.selectListCk.splice(0, this.selectListCk.length);
      this.$children[0]._data.board_title_warn = false;


      // $('select-company').find('options:first').attr('selected', 'selected');
      // co.options[co.selectedIndex].text = '선택';
      // te.options[te.selectedIndex].text = '선택';
      // mv.options[mv.selectedIndex].text = '선택';
      // // console.log('this.$children[0]._data.__proto__:', this.$children[0]._data.__proto__);
      // console.log('this.$children[0]:', this.$children[0]);
      // console.log('this.$children[0].selectListMv.length:', this.$children[0].selectListMv.length);
      // this.$children[0].selectListCk
      // console.log($('select-company').find('option:first').attr('selected', 'selected'));      
    },
    modifyModal(index) {
      let input = document.getElementById('search-title-md')
      let title = this.isBookmarkList[index].title
      let contents = this.isBookmarkList[index].contents.toString()
      this.index = this.isBookmarkList[index]

      input.value = title
      this.selectListCo.splice(0, this.selectListCo.length);
      this.selectListTe.splice(0, this.selectListTe.length);
      this.selectListMv.splice(0, this.selectListMv.length);
      this.selectListCk.splice(0, this.selectListCk.length);
      this.$children[2]._data.board_title_warn = false;

      console.log('this.isBookmarkList[index]:', this.isBookmarkList[index]);
      // if ( 'Amazon'.indexOf(contents) > -1 ) {
      //   this.selectListCo.push(contents)
      // } else if ( 'AI', 'Big-data', 'Cloud', 'E-commerce', 'VR'.indexOf(contents > -1) ) {
      //   this.selectListTe.push()
      // } else if ( 'invest', 'acquire'.indexOf(contents > -1) ) {
      //   this.selectListMv.push()
      // }
    },

    removeBoard(index) {
      let con = confirm('보드를 삭제하시겠습니까?')
      if ( con === true ) {
        this.isBookmarkList.splice(index, 1)
      }
    },
    goToArticle(index) {
      this.$router.push( { path: '/search/:id' } )
      // this.$router.push( { path: `/search/:${index}` } )
    }  
  }

}
</script>

<style lang="scss" scoped>

.article-container {
  padding: 0 55px 0 55px; 

  .section-header {
    span {
      font-size: 25px;
      font-weight: 400;
      color: #FF8C00;
    }
    h1 {
      margin: 35px 0 0 0;
      font-size: 24px;
      font-weight: 500;
    }
    .help-bookmark {
      float: right;
      border: none;
      background: none;
      .fa-question-circle {
        font-size: 14px;
        line-height: 2;
        color: #FF8C00;
      }
      .help-text {
        font-size: 14px;
        font-weight: 600;
        line-height: 2;
        color: #808080;
      }
    }
  }
  #new-bookmark {
    cursor: pointer;
  }
  .board-list {
    .board {
      margin: 15px 10px 0 0;
      min-height: 100px;
      display: inline-block;
      width: 225px;
      height: 120px;
      vertical-align: top;
      border: 1.5px solid #e8e8e8;
      background-color: #fff;
      box-shadow: 2px 4px 10px rgba(0,0,0,.3);
      padding: 15px;
    }
    .board-new {
      cursor: pointer;
    }
    .board-added {
      margin: 15px 10px 0 0;
      min-height: 100px;
      display: inline-block;
      width: 225px;
      height: 120px;
      vertical-align: top;
      border: 1.5px solid #e8e8e8;
      background-color: #fff;
      box-shadow: 2px 4px 10px rgba(0,0,0,.3);
      padding: 15px;
    }
    .add-keyword {
      text-align: center;
      margin: 15px auto;
      font-size: 15px;
      font-weight: 600;
      color: #777;
    }
    .add-btn-group {
      .add-list {
        display: block;
        margin: 0 auto;
        background: none;
        border: none;
        color: #FF8C00;
      }
    }
    .board-title {
      color: #777;
      border-bottom: 1px solid #808080;
      padding: 0 0 10px 3px;
      span {
        float: right;
        padding-right: 8px;
        color: #FF8C00;
        font-weight: 500;
      }
    }
    .list-group {
      .list-name {
        display: inline-block;
        width: 160px;
        padding-left: 3px;
        margin-top: 10px;
        color: #FF8C00;
        font-weight: 500;
      }
      .list-btn-group {
        width: 30px;
        float: right;
        button {
          margin-top: 10px;
          color: #305050;
          background: none;
          border: none;
        }
      }
    }
  }
  article {
    margin: 30px 0 30px 0;
    .col-md-12 {
      padding: 0;
    }
  }
  .footer {
    position: absolute;
    bottom: 0;
  }
}
</style>




