<template>
  <section class="main">
    <div class="alert-container">
      <div class="alert-header">
        <div class="alert alert-warning alert-dismissible" role="alert">
          <button type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
          <strong>Welcome!</strong> Find Big 5를 찾아주셔서 감사합니다.
        </div>
      </div>        
    </div>

    <main-hottopic></main-hottopic>
    <main-invest></main-invest>
    <main-sns></main-sns>
    <main-stock></main-stock>

    <app-footer></app-footer>

    <!-- <main-result></main-result> -->
    <!-- <main-bookmark></main-bookmark> -->
    <!-- <main-saved></main-saved> -->
  </section>
</template>

<script>
import AppFooter from './Footer'
import MainHottopic from './MainHottopic'
import MainInvest from './MainInvest'
import MainSns from './MainSns'
import MainStock from './MainStock'
import { makeChart } from '../chart.js'
// import MainBookmark from './MainBookmark'
// import MainResult from './MainResult'
// import MainSaved from './MainSaved'
// import ModalGuide from './ModalGuide'

export default {
  name: 'MainPage',
  components: {
    AppFooter, MainHottopic, MainInvest, MainSns, MainStock, 
    // MainBookmark, MainResult, MainSaved, ModalGuide
  },
  watch: {
    '$route' (to, from) {
      this.id = to.params.id;
      this.isChartShow()
    }
  },
  // created () {
  //   this.$nextTick(() => {
  //     makeChart()
  //   })
  // },
  mounted () {
    // this.$nextTick(() => {  
      document.getElementById('twitter-amazon').classList.add('active', 'in')
    // console.log('this.$route:', this.$route);  
    // ,makeChart()    
    // })    
  },
  data() {
    return {
      isShowCo: false,
      isShowTe: false,
      isChangeBg: {},
      id: this.$route.params.id
    }
  },
  methods: {
    isChartShow() {
      if ('/'.indexOf(this.$route.params) > -1 ) {
        makeChart()        
      }
    },
    isToggleCo() {
      this.isShowCo = !this.isShowCo;
      if ( this.isShowCo === true ) {
        this.isShowTe = false;
      }
    },
    isToggleTe() {
      this.isShowTe = !this.isShowTe;
      if ( this.isShowTe === true ) {
        this.isShowCo = false;
      }
    },
    changeBg() {
      this.changeBg = {
        background: '#ffbb60'
      }
    },
    // goToMain() {
    //   this.$router.push( { name: 'Main' } )
    // },
  }
}
</script>

<style lang="scss" scoped>
.main {
  height: 100%;
  min-height: 100%;
  background-color: #F4f6f6;
}
.alert-container {
  padding: 0 55px 0 55px; 
  margin-top: -70px;

  .alert-header {
    width: 955px;
    position: relative;
    top: 70px;
    .alert {
      margin: 0;
    }
  }
}
// .logout {
