<template>
  <header class="header">
    <nav class="navbar navbar-default navbar-static-top" role="navigation">
      <div class="input-group">
        <input ref="input" @keydown.enter="goToResult" :value="isHeaderData" type="text" class="input-search" placeholder="Search">
        <span @click="goToResult" class="glyphicon glyphicon-search" aria-hidden="true"></span>
      </div>
      <div class="btn-group">
        <button @click="resetModal" class="btn btn-link" data-toggle="modal" data-target="#detail_modal">
          상세검색
        </button>
      </div>
      <a id="detail-search-popover" role="button" data-toggle="popover" data-placement="bottom">
      <!-- <a itabindex="0" id="detail-search-popover" role="button" data-toggle="popover" data-placement="bottom" data-trigger="focus"> -->
        <i class="fa fa-info-circle fa-2x" aria-hidden="true"></i>
      </a>
      <div class="btn-translate">
        <form name="language">
          <div class="btn-group btn-translate" data-toggle="buttons">
            <label class="btn btn-default" id="english">
              <input type="radio" name="lan_options" value="english" autocomplete="off">
              영어 원문
            </label>
            <label class="btn btn-default" id="korean">
              <input type="radio" name="lan_options" value="korean" autocomplete="off">한글 번역
            </label>
          </div>
        </form>
      </div>
    </nav>
    <modal-detail-search></modal-detail-search>
  </header> 
</template>

<script>
import {mapGetters, mapMutations, mapActions} from 'vuex'

import ModalSearch from './ModalSearch'
import ModalDetailSearch from './ModalDetailSearch'
export default {
  name: 'HeaderMenu',
  // mounted () {
  //   $(function () {
  //     $('[data-toggle="popover"]').popover({
  //       // container: 'body',
  //       title: '상세검색',
  //       content: "상세검색은 검색키워드를 여러 개 중첩하여 검색할 수 있게 하는 기능으로, 더욱 높은 정확도를 원하는 사람들에게 유용합니다.<br><br>1. ‘구글’의 ‘AI’ 동향이 궁금하다면? => [회사] 카테고리에서 ‘Google’을 선택하고, [기술] 카테고리에서 ‘AI'를 선택한 다음, 검색을 누릅니다.<br>2. ‘아마존’의 ‘투자/인수’ 동향이 궁금하다면? =>  [회사] 카테고리에서 ‘Amazon’을 선택하고, [행동] 카테고리에서 ‘Invest’, ’Acquire’를 선택한 다음, 검색을 누릅니다.<br>3. 만약 원하는 키워드가 분류된 카테고리에 없을 경우, [개인용어] 섹션에 직접 입력할 수 있습니다.<br><br>* 저희 엔진은 BIG5 기업에 초점을 맞추어 작동하므로 BIG5와 관련이 없는 키워드에 대한 검색결과는 불만족스러울 수 있습니다.",
  //       html: true
  //     })
  //   });
  // },
  components: {
    ModalSearch, ModalDetailSearch
  },
  computed: {
    ...mapGetters([
      'isHeaderData',
      'selectListCo',
      'selectListTe',
      'selectListMv',
      'selectListCk',
    ])
  },
  methods: {
    ...mapMutations([

    ]),
    resetModal() {
      let notice = document.getElementById('p-s-k')

      notice.style.color = '#b3b3b3'
      this.selectListCo.splice(0, this.selectListCo.length);
      this.selectListTe.splice(0, this.selectListTe.length);
      this.selectListMv.splice(0, this.selectListMv.length);
      this.selectListCk.splice(0, this.selectListCk.length);
      
      if(notice.style.display === 'none') {
        notice.style.display = 'block'
      }
    },

    goToResult() {
      if ( this.$refs.input.value === '' ) {
        this.$refs.input.placeholder = '검색어를 입력해주세요.'
        this.$refs.input.focus()
      } else {
        this.$router.push( { path: '/search/:id' } )
        this.$refs.input.value = '';
        this.$refs.input.placeholder = 'Search'        
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.navbar-default {
  position: relative;
  top: 0;
  left: 0;
  z-index: 1;
  height: 65px;
  background-color: #fff;
  border-bottom: 1px solid #EAEDED;
  margin-bottom: 0;
}
.input-group {
  position: relative;
  width: 480px;
  display: inline-block;
  padding: 13px 0 15px 55px;

  .input-search {
    width: 410px;
    height: 38px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: hsl(0, 0%, 33%);
    background-color: hsl(0, 0%, 100%);
    background-image: none;
    border: 1px solid hsl(0, 0%, 80%);
    border-radius: 4px;
  }

  .input-group-btn {
    width: 20px;
    display: inline-block;
  }
  .glyphicon-search {
    color: #BFC9CA;
    font-size: 20px;  
    display: inline;
    position: absolute;  
    right: 25px;
    top: 22px;
    cursor: pointer;
  }
}
#detail-search-popover {
  margin-left: 20px;
  position: absolute;
  top: 45px;
  left: 565px;
  .fa-info-circle {
    width: 30px;
    color: #2C3E50;
    position: absolute;
    top: -26px;
    left: -12px;
  }
}
.btn-translate {
  display: inline-block; 
  position: relative;
  left: 147px;
}

</style>





