// import Vue from 'vue'
// import Router from 'vue-router'
import Main from './components/MainPage'
import MainResult from './components/MainResult'
import MainBookmark from './components/MainBookmark'
import MainSaved from './components/MainSaved'
import MainStock from './components/MainStock'

// Vue.use(Router)

// export default new Router({
//   routes: [
  export const routes = [  
    { path: '*', component: Main },
    { path: '/', component: Main },
    {
      path: '/main',
      name: 'Main',
      components: {
          default: Main, MainStock,
      children: [
          { path: '/category:id', component: MainResult },
          { path: '/category:id', component: MainResult },
          { path: '/category:id', component: MainResult },
          { path: '/category:id', component: MainResult },
          { path: '/category:id', component: MainResult },
          { path: '/search_favorite', component: MainBookmark },
          { path: '/save_board', component: MainSaved },
      ]
      },
    },
    // {
    //   path: '/',
    //   name: 'Stock',
    //   components: {
    //     default: MainStock
    //   }
    // },
    {
      path: '/category/:id',
      name: 'Category',
      components: {
          default: MainResult ,
      }
    },
    {
      path: '/search/:id',
      name: 'Search',
      components: {
          default: MainResult ,
      }
    },
    {
      path: '/search_favorite',
      name: 'Favorite',
      components: {
          default: MainBookmark ,
      }
    },
    {
      path: '/save_board',
      name: 'Board',
      components: {
          default: MainSaved ,
      }
    },
  ]
// })



