<template>
  <div class="modal fade" ref="detail" id="detail_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="row">
          <div class="col-xs-7">
            <div class="modal-left">
              <div class="input-group">
                <div class="input-label">회사</div>
                <!-- <select @input="selectOptionCo" v-model="select_option_co.option" name="option" id="select-company-s" class="form-control keyword">
                  <option value="" selected>선택</option>
                  <optgroup v-for="option in options">
                    <option v-for="select in option.selects_co">{{ select }}</option>
                  </optgroup>
                </select> -->
                <select @input="selectOptionCo" name="" id="select-company-s" class="form-control keyword">
                  <option selected disabled hidden>선택</option>
                  <option>Amazon</option>
                  <option>Apple</option>
                  <option>Facebook</option>
                  <option>Google</option>
                  <option>Microsoft</option>
                </select>
              </div>
              <div class="input-group">
                <div class="input-label">기술</div>
                <!-- <select @input="selectOptionTe" v-model="select_option_te.option" name="option" id="select-tech-s" class="form-control keyword">
                  <option value="" selected>선택</option>
                  <optgroup v-for="option in options" >
                    <option v-for="select in option.selects_te">{{ select }}</option>
                  </optgroup>
                </select> -->
                <select @input="selectOptionTe" name="" id="select-tech-s" class="form-control keyword">
                  <option selected disabled hidden>선택</option>
                  <option>Ai</option>
                  <option>Big-Data</option>
                  <option>Cloud</option>
                  <option>E-commerce</option>
                  <option>VR</option>
                </select>
              </div>
              <div class="input-group">
                <div class="input-label">행동</div>
                <!-- <select @input="selectOptionMv" v-model="select_option_mv.option" name="option" id="select-mv-s" class="form-control keyword">
                  <option value="" selected>선택</option>
                  <optgroup v-for="option in options">
                    <option v-for="select in option.selects_mv">{{ select }}</option>
                  </optgroup>
                </select> -->
                <select @input="selectOptionMv" name="" id="select-mv-s" class="form-control keyword">
                  <option selected disabled hidden>선택</option>
                  <option>invest</option>
                  <option>acquire</option>
                </select>
              </div>
              <div class="input-group">
                <div class="input-label">개인용어</div>
                <input ref="custom" @keydown.enter="customKeyword" :value="custom_data" id="custom-keyword-s" class="keyword-select personal-keyword" type="text" placeholder="입력" maxlength="20">
                <button @click="customKeyword" id="add-keyword" class="add-keyword btn btn-default">추가</button>
              </div>
            </div>
          </div>
          <div class="col-xs-5">
            <div class="modal-right">
              <div id="keyword-nodes">
                <div ref="psk" class="p-s-k" id="p-s-k">키워드를 선택해주세요</div>
                <div v-for="(list, index) in selectListCo" class="keyword-btn-group">
                  <button @click="removeButtonCo(index)" class="keyword" type="button">
                    {{ list }}
                    <span class="close-keyword">x</span>
                  </button>
                </div>
                <div v-for="(list, index) in selectListTe" class="keyword-btn-group">
                  <button @click="removeButtonTe(index)" class="keyword" type="button">
                    {{ list }}
                    <span class="close-keyword">x</span>
                  </button>
                </div>
                <div v-for="(list, index) in selectListMv" class="keyword-btn-group">
                  <button @click="removeButtonMv(index)" class="keyword" type="button">
                    {{ list }}
                    <span class="close-keyword">x</span>
                  </button>
                </div>
                <div v-for="(list, index) in selectListCk" class="keyword-btn-group">
                  <button @click="removeButtonCk(index)" class="keyword" type="button">
                    {{ list }}
                    <span class="close-keyword">x</span>
                  </button>
                </div>
              </div>
              <button @click="searchPage" id="search-keyword" class="btn btn-default">검색</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'ModalDetailSearch',
  data() {
    return {
      custom_data: '',
    }
  },
  computed: {
    ...mapGetters([
      'selectListCo',
      'selectListTe',
      'selectListMv',
      'selectListCk',
      'boardTitleWarn',
    ])
  },
  methods: {
    searchPage() {
      let notice = document.getElementById('p-s-k')
      
      if ( this.selectListCo.length === 0 && this.selectListTe.length === 0  && this.selectListMv.length === 0 && this.selectListCk.length === 0) {
        // alert('키워드를 선택해주시기 바랍니다.') 
        notice.style.color = 'red'
        return false
      } else { 
        this.$refs.detail.style.display = 'none'
        this.$router.push( { path:'/search/:id' } )
      }  
    },

    removeButtonCo(index) {
      let notice = document.getElementById('p-s-k')
      
      if ( this.selectListCo.length >= 0 ) {
        this.selectListCo.splice(index, 1)
        if (this.selectListCo.length === 0 && this.selectListTe.length === 0 && this.selectListMv.length === 0 && this.selectListCk.length === 0) {
        notice.style.display = 'block'
        }
      } 
    },

    removeButtonTe(index) {
      let notice = document.getElementById('p-s-k')
      
      if ( this.selectListTe.length >= 0 ) {
        this.selectListTe.splice(index, 1)
        console.log('this.selectListCo.length:', this.selectListCo.length);
        if (this.selectListCo.length === 0 && this.selectListTe.length === 0 && this.selectListMv.length === 0 && this.selectListCk.length === 0) {
        notice.style.display = 'block'
        }
      } 
    },

    removeButtonMv(index) {
      let notice = document.getElementById('p-s-k')
      
      if ( this.selectListMv.length >= 0 ) {
        this.selectListMv.splice(index, 1)
        if (this.selectListCo.length === 0 && this.selectListTe.length === 0 && this.selectListMv.length === 0 && this.selectListCk.length === 0) {
        notice.style.display = 'block'
        }
      } 
    },

    removeButtonCk(index) {
      let notice = document.getElementById('p-s-k')
      
      if ( this.selectListCk.length >= 0 ) {
        this.selectListCk.splice(index, 1)
        if (this.selectListCo.length === 0 && this.selectListTe.length === 0 && this.selectListMv.length === 0 && this.selectListCk.length === 0) {
        notice.style.display = 'block'
        }
      } 
    },

    //-------------------------------------------------- 선택항목 버튼으로 보여주기 함수
    selectOptionCo(e) {
      let co = document.getElementById('select-company-s')
    
      if (co.options[co.selectedIndex].text) {
        this.$refs.psk.style.display = 'none'
        if ( this.selectListCo.indexOf(e.target.value) === -1 ) {
          this.selectListCo.push(e.target.value)
        } else { 
          alert('키워드가 중복되었습니다. 다른 키워드를 선택해주시기 바랍니다.')
          // return false 
        }
      }  
    },

    selectOptionTe(e) {
      let te = document.getElementById('select-tech-s');
      console.log('e.target.value:', e.target.value);
      console.log('this.selectListTe.indexOf(e.target.value):', this.selectListTe.indexOf(e.target.value));

      if (te.options[te.selectedIndex].text) {
        this.$refs.psk.style.display = 'none'
        if ( this.selectListTe.indexOf(e.target.value) === -1 ) {
          this.selectListTe.push(e.target.value)
        } else if ( e.target.value === '' ) {
          return false
        } else {
          alert('키워드가 중복되었습니다. 다른 키워드를 선택해주시기 바랍니다.')
        }
      }
    },

    selectOptionMv(e) {
      let mv = document.getElementById('select-mv-s');

      if (mv.options[mv.selectedIndex].text) {
        this.$refs.psk.style.display = 'none' 
        if (this.selectListMv.indexOf(e.target.value) === -1 ) {
          this.selectListMv.push(e.target.value)
        } else {
          alert('키워드가 중복되었습니다. 다른 키워드를 선택해주시기 바랍니다.')          
        }
      }
    },

    customKeyword() {
      let ck = document.getElementById('custom-keyword-s');
      console.log('ck.value:', ck.value);
      if ( this.$refs.custom.value ) {
        this.$refs.psk.style.display = 'none'         
        this.selectListCk.push(this.$refs.custom.value)
      }
    },
  }
}
</script>

<style lang="scss" scoped>
.col-xs-5 {
  padding: 0;
}
.col-xs-7 {
  padding-right: 0;
}
.modal-dialog {
  position: relative;
  margin-top: 20vh;
  z-index: 9999;
  .modal-content {
    background-color: #fff;
    box-shadow: 0 5px 20px rgba(0,0,0,.5);
    border-radius: 8px;    
  }
  .modal-left {
    padding: 30px 0 30px 25px;  
    border-right: 1px solid #808080;
    .input-group {
      .input-label {
        margin-bottom: 5px;
        margin-left: 2px;
      }
      .add-keyword {
        margin-left: 12px;
        height: 34px;
      }
    }
    select, input {
      width: 230px;
      margin-bottom: 20px;
    }
    .keyword-select {
      border: 1px solid #ccc;
      border-radius: 4px;
      height: 34px;
      margin-bottom: 18px;
      padding: 6px 8px;
    }
  }
  .modal-right {
    padding: 15px 0 0 0;
    text-align: center;
    #keyword-nodes {
      height: 190px;
      padding-top: 20px;
      text-align: left;
      padding-left: 30px;
      }
      .keyword-btn-group {
        margin-bottom: 10px;
        .keyword {
          display: block;
          background: #f5f5f5;
          border-radius: 5px;
          display: inline-block;
          min-width: 10px;
          padding: 3px 7px;
          font-size: 12px;
          font-weight: 700;
          border: none;
          .close-keyword {
            padding-left: 5px;
            font-size: 12px
          }
        }
      }
      .p-s-k {
        color: #b3b3b3;
      }
    }
    #search-keyword {
      margin-top: 70px;
    }
    #board-name {
      border: 1px solid #ccc;
      border-radius: 4px;
      height: 34px;
      padding: 6px 8px;
      width: 200px;
    }
    .input-label {
      margin: 0 0 5px 2px;
    }
  }
</style>





