<template>
  <div class="modal fade" id="star_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="row">
          <div class="col-xs-6 col-left">
            <div class="modal-left">
              <div class="img-wrapper">
                <img src="https://static01.nyt.com/images/2017/11/30/business/30CHINADRONE3/30CHINADRONE3-facebookJumbo.jpg" alt="article image">
              </div>
              <h4 class="headline">Drone Maker D.J.I. May Be Sending Data to China, U.S. Officials Say</h4>
              <p>NewYorkTimes / 2017-11-30 07:40</p>
            </div>
          </div>
          <div class="col-xs-6 col-right">
            <div class="modal-right">
              <div class="choose-board">
                <h3 class="modal-right-head">보드 선택</h3>
                <div class="board-names">
                  <div class="name-board">
                    <p>보드 네임</p>
                    <button class="save">저 장</button>
                  </div>
                  <div class="name-board">
                    <p>보드 네임</p>
                    <button class="save">저 장</button>
                  </div>
                </div>
                <div class="add-board">
                  <div class="modal-right-head">보드 추가</div>
                  <input ref="input" @keydown.enter="makeBoard" type="text" id="name-board" placeholder="보드명" maxlength="20">
                  <div v-show="board_title_warn" ref="pmt" class="p-m-t">{{ please_board_title }}</div>
                  <button @click="makeBoard" id="add-board">만들기</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  
  name: 'ModalSearch',
  data() {
    return {
      board_title_warn: false,
      please_board_title: '',
    }
  },
  computed: {
    ...mapGetters([
      'isBoardList',
    ])
  },
  methods: {
    ...mapMutations([
      // 'makeSaveBoard',
      'makeStarSaveBoard',
    ]),
    makeBoard() {
      let e = this.$refs.input.value.trim()
      let obj = {}    
      if(e === '') {
        this.board_title_warn = true
        this.please_board_title = '보드명 입력은 필수입니다.'
        this.$refs.input.focus()
      } else {
        let confirmBoard = confirm(`새로운 보드 ${e}(을)를 생성 하시겠습니까?`)
        if (confirmBoard === true ) {
          let modal = document.getElementById('favorite-modal')
          let modal_s = document.getElementById('star_modal')
          if (modal) {
            modal.style.display = 'none';
          } else if (modal_s) {
            modal_s.style.display = 'none';
          }
          obj.title = e
          this.isBoardList.unshift(obj)
          this.$router.push( { path: '/save_board' } )      
          console.log('this.isBoardList:', this.isBoardList);
        }   
        this.board_title_warn = false
        // this.makeStarSaveBoard(e)
        // this.$router.push( { path: '/save_board' } )
      }
    },
  }
}
</script>

<style lang="scss" scoped>
.col-left {
  padding-right: 30px;
}
.col-right {
  padding-right: 15px;
}
.modal-dialog {
  position: relative;
  margin-top: 12vh;
  z-index: 9999;
  .modal-content {
    background-color: #fff;
    box-shadow: 0 5px 20px rgba(0,0,0,.5);
    border-radius: 8px;    
  }
  .modal-left {
    padding: 30px 0 30px 15px;  
    margin-top: 25%;
    // border-right: 1px solid #808080;
    .img-wrapper {
      height: 230px;
      overflow: hidden;
      position: relative;
      img {
        position: absolute;
        left: -100%;
        right: -100%;
        top: -100%;
        bottom: -100%;
        margin: auto;
        height: 100%;
      }
    }
    .headline {
      font-size: 18px;
      font-weight: 500;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
  .modal-right {
    margin-left: -30px;
    min-height: 510px;
    padding: 15px 0;
    border-left: 1px solid #808080;
    .modal-right-head {
      padding: 0 10px;
      margin-bottom: 15px;
      font-size: 18px;
      font-weight: 700;
    }
    .board-names {
      min-height: 300px;
      margin-bottom: 15px;
      border-bottom: 1px solid #808080;
      padding: 0 20px;
      .name-board {
        display: block;
        position: relative;
        border-bottom: 1px solid #d4d4d4;
        p {
          display: inline-block;
          font-size: 16px;
          letter-spacing: -.25px;
          line-height: 34px;
          margin: 8px 0;
          max-width: 180px;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: middle;
          white-space: nowrap;
        }
        .save {
          background: #ff8c00;
          color: #fff;
          font-size: 14px;
          touch-action: manipulation;
          cursor: pointer;
          border: none;
          border-radius: 3px;
          float: right;
          font-weight: bold;
          height: 36px;
          line-height: 18px;
          margin-top: 6px;
          padding: 9px 12px;
          white-space: nowrap;
        }
      }
    }
    .add-board {
      #name-board {
        display: block;
        width: 100%;
        border: 1px solid hsl(0, 0%, 83%);
        padding: 10px 10px;
        border-right: none;
        border-left: none;
        margin-bottom: 15px;
      }
      #add-board {
        background: #ff8c00;
        color: #fff;
        font-size: 14px;
        touch-action: manipulation;
        cursor: pointer;
        border: none;
        border-radius: 3px;
        float: right;
        margin-right: 15px;
        margin-bottom: 15px;
        padding: 10px;
      }
      .p-m-t {
        color: red;
        padding-left: 10px;
        font-size: 12px;
      }
    }
   }
  }
</style>





