<template>
  <div class="saved">
    <div class="article-container">
      <div class="section-header col-xs-12">
        <h1 class="main-header">
          <span>_</span> 저장한 뉴스
        </h1>
      </div>
      <div class="article-saved">
        <div class="row">
          <div class="col-xs-12">
            <div class="article-saved-list">
              <article>
                <ul class="board-list">
                  <li id="new-board" v-for="(list, index) in isBoardList" class="board-added">
                    <h4 @click="goToArticle(index)" class="board-title">{{ list.title }}</h4>
                    <div class="list-btn-group">
                      <span @click="goToArticle(index)" class="list-numbers">0</span>
                      <button @click="openModifyBoard(index)" class="edit-list" type="button" data-toggle="modal" data-target="#modify-board">
                        <i class="fa fa-cog fa-2x" aria-hidden="true"></i>
                      </button>
                      <button class="list-delete" type="button">
                        <i @click="removeBoard" class="fa fa-trash fa-2x" aria-hidden="true"></i>
                      </button>
                    </div>
                  </li>
                  <li @click="resetBoard" class="board board-new" data-toggle="modal" data-target="#favorite-modal">
                    <div class="add-board">보드 추가</div>
                    <div class="add-btn-group">
                      <button class="add-list" type="button">
                        <span class="fa-stack hightlight">
                          <i class="fa fa-circle-thin fa-stack-2x"></i>
                          <i class="fa fa-plus fa-stack-1x"></i>
                        </span>
                      </button>
                    </div>
                  </li>
                </ul>
              </article>
            </div>
          </div>
        </div>
      </div>
    </div>
    <modal-board></modal-board>
    <modal-board-modify :index="index"></modal-board-modify>
    <!-- <modal-board-modify :isBoardList="isBoardList"></modal-board-modify> -->
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

import ModalBoard from './ModalBoard'
import ModalBoardModify from './ModalBoardModify'

export default {
  name: 'MainSaved',
  components : {
    ModalBoard, ModalBoardModify,
  },
  data() {
    return {
      board_title: '',
      index: '',
    };
  },
  computed: {
    ...mapGetters([
      'isInputData',
      'isBoardTitle',
      'isBoardList',
    ])
  },
  methods: {
    ...mapMutations([

    ]),
    resetBoard() {
      let val = document.getElementById('save-title')
      val.value = '';
      this.$children[0]._data.board_title_warn = false;
      console.log('this.$children.board_title_warn:', this.$children);
    },

    openModifyBoard(index) {
      this.index = this.isBoardList[index]
      this.$children[1]._data.board_title_warn = false;
      let input = document.getElementById('modify-save-title')
      input.value = this.isBoardList[index].title
    },

    removeBoard(index) {
      let con = confirm('보드를 삭제하시겠습니까?')
      if (con === true) {
        this.isBoardList.splice(index, 1);
      } 
    },

    goToArticle(index) {
      this.$router.push( { path: '/search/:id' } )
      // this.$router.push( { path: `/search/:${index}` } )
    }  
  }
}
</script>

<style lang="scss" scoped>
.article-container {
  padding: 0 55px 0 55px; 

  .section-header {
    span {
      font-size: 25px;
      font-weight: 400;
      color: #FF8C00;
    }
    h1 {
      margin: 35px 0 5px 0;
      font-size: 24px;
      font-weight: 500;
    }
  }
  .board-list {
    .board {
      margin: 15px 10px 0 0;
      min-height: 100px;
      display: inline-block;
      width: 225px;
      height: 120px;
      vertical-align: top;
      border: 1.5px solid #e8e8e8;
      background-color: #fff;
      box-shadow: 2px 4px 10px rgba(0,0,0,.3);
      padding: 15px;
    }
    .board-new {
      cursor: pointer;
    }
    .board-added {
      margin: 15px 10px 0 0;
      min-height: 100px;
      display: inline-block;
      width: 225px;
      height: 120px;
      vertical-align: top;
      border: 1.5px solid #e8e8e8;
      background-color: #fff;
      box-shadow: 2px 4px 10px rgba(0,0,0,.3);
      padding: 20px 15px 15px 15px;
      .board-title {
        cursor: pointer;
        text-align: center;
        font-size: 18px;
        color: #777;
        padding: 2px 0 15px 0;
        border-bottom: 1px solid #808080;
      }
      .list-btn-group {
        padding-top: 10px;
        span {
          display: inline-block;
          padding-right: 58px;
          width: 35px;
          color: #FF8C00;
          font-size: 25px;          
          font-weight: 500;
          line-height: 1.5;
        }
        button {
          // margin-top: 10px;
          display: inline-block;
          color: #305050;
          background: none;
          border: none;
        }
        .edit-list {
          width: 65px;
        }
        .list-delete {
          padding-left: 20px;
          float: right;
        }
      }
    }
    .add-board {
      text-align: center;
      margin: 15px auto;
      font-size: 15px;
      font-weight: 600;
      color: #777;
    }
    .board-keyword {
      text-align: center;
      margin: 10px auto;
      font-size: 15px;
      font-weight: bold;
      color: #305050;        
    }
    .add-btn-group {
      .add-list {
        display: block;
        margin: 0 auto;
        background: none;
        border: none;
        color: #FF8C00;
      }
    }
    .list-group {
      .list-name {
        display: inline-block;
        padding-left: 3px;
        margin-top: 10px;
        color: #FF8C00;
        font-weight: 500;
      }
    }
  }
  article {
    margin: 31px 0 30px 0;
    .col-md-12 {
      padding: 0;
    }
  } 
}
</style>




