export function makeChart() {
  let result = new TradingView.MediumWidget({
    "container_id": "tv-medium-widget-f22ed",
    "symbols": [
      [
        "Amazon",
        "NASDAQ:AMZN|1d"
      ],
      [
        "Apple",
        "NASDAQ:AAPL|1d"
      ],
      [
        "Facebook",
        "NASDAQ:FB|1d"
      ],
      [
        "Google",
        "NASDAQ:GOOGL|1d"
      ],
      [
        "Microsoft",
        "NASDAQ:MSFT|1d"
      ]
    ],
    "greyText": "쿼트 제공",
    "gridLineColor": "#e9e9ea",
    "fontColor": "#83888D",
    "underLineColor": "rgba(252, 229, 205, 1)",
    "trendLineColor": "rgba(255, 152, 0, 1)",
    "width": "892px",
    "height": "500px",
    "locale": "kr"
  });
    return result;
}
